@import "mixins";
@import "vars";

@media(max-width: 1440px) {
  .header {

    &__main {
      margin-right: 0;
    }
    &__buttons {
      margin-right: 0;
    }
  }

  .header__main__nav__list__item:not(:last-child) {
    margin-right: 18px;
  }

  .advantages__list__item h3 {
    width: 100%;
  }

  .dentists__list {
  grid-template-rows: 540px;
  gap: 30px;
    &__item {
      &__link {
        padding-top: 90%;
        padding-bottom: 75%;
        &__info {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          min-height: 230px;
          &__btn {
            position: relative;
            z-index: 3;
          }
          &__text {
            &__title {
              width: 110%;
            }
          }
        }
      }
    }
  }

  .footer__text {
    margin-right: 15px;
  }

  .footer__services {
    margin-right: 15px;
  }


}


